//script部分
    import avatar from "../../components/yq-avatar/yq-avatar.vue";
    export default {
        data() {
            return {
                url: "",
				status:''
            }
        },
		onLoad(option) {
			//console.log(option.pic)
			this.status=option.status
			let _this=this;
			if(option.status==0){
				_this.url=uni.getStorageSync('headpic')
			}else{
				this.url=option.pic;
			}
		},
		computed: {
			isModel:function(){
				return this.$store.state.model;
			},
		},
        methods: {
            myUpload(rsp) {
                this.url = rsp.path; //更新头像方式一
                //rsp.avatar.imgSrc = rsp.path; //更新头像方式二
				//console.log(this.url)
				let _this=this;
				let userType=uni.getStorageSync('userType')
				let openId=uni.getStorageSync('openid');
				if(this.status==0){
					uni.setStorageSync('picUrl',rsp.path)
					uni.navigateBack({
						delta:1
					})
				}else{
					uni.uploadFile({
					    url: _this.$store.state.baseurl+'/user/updateHeadPicUrl',
						filePath: rsp.path,
						name: 'file',
					    formData: {
							openId:openId
						},
					    method: 'POST',
					    header: {
					        "Content-Type": "multipart/form-data"
					    },
					    success: (res) => {
							this.$store.commit('setQiangSt',false);
							let type=uni.getStorageSync('userType');
							if(type==2){
								uni.navigateTo({
									url:'/pages/index/index'
								})
							}else if(type==3){
								uni.navigateTo({
									url:'/components/staffpages/index'
								})
							}
					    }
					});
				}
            },
			goback:function(){
				uni.navigateBack({
					delta:1
				})
			}
        },
        components: {
            avatar
        }
    }
